import React from 'react';
import Chart from 'chart.js';
import PropTypes from 'prop-types';

class LineChart extends React.Component {
  componentDidMount() {
    let theDatasets;
    if (this.props.datasets) {
      theDatasets = this.props.datasets;
    } else {
      theDatasets = [{
          label: this.props.datasetLabel,
          fill: this.props.fill,
          lineTension: this.props.lineTension,
          backgroundColor: this.props.backgroundColor,
          borderColor: this.props.borderColor,
          borderCapStyle: this.props.borderCapStyle,
          borderWidth: this.props.borderWidth,
          pointBorderColor: this.props.pointBorderColor,
          pointBackgroundColor: this.props.pointBackgroundColor,
          pointBorderWidth: this.props.pointBorderWidth,
          pointHoverRadius: this.props.pointHoverRadius,
          pointHoverBackgroundColor: this.props.pointHoverBackgroundColor,
          pointHoverBorderColor: this.props.pointHoverBorderColor,
          pointHoverBorderWidth: this.props.pointHoverBorderWidth,
          pointRadius: this.props.pointRadius,
          pointStyle: this.props.pointStyle,
          showLine: this.props.showLine,
          data: this.props.data,
          xAxisID: this.props.xAxisID,
          yAxisID: this.props.yAxisID
      }];
    }
    const data = {
      labels: this.props.axisLabels,
      datasets: theDatasets
    };
    const ctx = document.getElementById(this.props.id).getContext('2d');
    const line = new Chart(ctx, {
      type: 'line',
      options: this.props.options,
      data
    });
  }

  render() {
    return (
      <canvas id={this.props.id} width={this.props.width} height={this.props.height} />
    );
  }
}

LineChart.propTypes = {
  id: PropTypes.string,
  width: PropTypes.string,
  height: PropTypes.string,
  datasets: PropTypes.array,
  datasetLabel: PropTypes.string,
  axisLabels: PropTypes.array,
  lineTension: PropTypes.number,
  data: PropTypes.array,
  backgroundColor: PropTypes.string,
  borderColor: PropTypes.string,
  borderCapStyle: PropTypes.string,
  borderWidth: PropTypes.number,
  fill: PropTypes.bool,
  pointBackgroundColor: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.string
  ]),
  pointBorderColor: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.string
  ]),
  pointBorderWidth: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.number
  ]),
  pointRadius: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.number
  ]),
  pointHoverRadius: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.number
  ]),
  pointHoverBackgroundColor: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.string
  ]),
  pointHoverBorderColor: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.string
  ]),
  pointHoverBorderWidth: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.number
  ]),
  pointStyle: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.string
  ]),
  showLine: PropTypes.bool,
  xAxisID: PropTypes.string,
  yAxisID: PropTypes.string,
  options: PropTypes.object
};

LineChart.defaultProps = {
  id: 'line-chart-0',
  width: '300',
  height: '200',
  lineTension: 0,
  fill: false,
  showLine: false,
  pointStyle: 'circle',
  options: {
    legend: {
      display: false,
        labels: {
          display: false
        }
    }
  }
};

export default LineChart;
